
.landing--root{
/*	background: url('../../assets/img/background_1.jpg');*/
	background-color: rgba(197, 197, 197, 0.2);
	height: 100vh;
/*	justify-content: center;
	display: flex;
	align-items: center;*/

}

.code--image--content{
	background-repeat: none;
}

.code--image--content img{
	max-height: 100vh;
}

.content--profile{
	height: 100vh;
	justify-content: center;
	display: flex;
	align-items: center;
}

.avatar--profile{
	height: 30vh;
	width: 30vh;
}

h3{
	font-size: 2.2rem;
	padding-top: 10px;
	text-align: center;
}

.vitae--content{
	text-align: center;
	width: 100%;
/*	display: flex;*/
}
.vitae--content .vitae--description{
	padding-left: 24px;
	padding-right: 24px;
	font-size: 1rem;
}

.vitae--content .vitae--description span{
	font-size: 0.8rem;
	margin: 0px;
	font-style: italic;
	opacity: 0.8;

}

.button--group{
	display: flex;
	padding: 16px;
	justify-content: center;
	flex-direction: column;

	align-items: center;
}

.button--group .anticon{
	font-size: 28px;
	color: #3b85e6;
}

.button--group div{
	display: flex;
	align-items: center;
	text-align: left;
	flex-direction: row;
	padding-bottom: 5px;
}
.button--group span, a{
	padding-left: 6px;
	color: inherit;
}



